//== DarkMode ==//
body {
	--bkg-color: white;
	--bkg-color-2: white;
    --text-color: #222;
	--text-color-blue: Blue;
	--border-color: #ddd;
	--box-shadow-1: #777777;
	--box-shadow-2: #c6c6c6;
	--selection-text-color: rgb(255, 82, 2);
	--selection-bkg-color: transparent;
}
  

