body.dark-theme {
	--bkg-color: #111;
    --bkg-color-2: black;
    --text-color: #eee;
	--text-color-blue: #0078ff;
	--border-color: #333;
	--box-shadow-1: black;
	--box-shadow-2: black;
	--selection-text-color: #f9ff5a;
	--selection-bkg-color: transparent;
}
@media (prefers-color-scheme: dark) {
    body {
        --bkg-color: #272727;
        --bkg-color-2: black;
        --text-color: #eee;
		--text-color-blue: #0078ff;
		--border-color: #333;
		--box-shadow-1: black;
		--box-shadow-2: black;
		--selection-text-color: #f9ff5a;
		--selection-bkg-color: transparent;
    }
    body.light-theme {
		// --bkg-color: #f3f3f3;
		--bkg-color: white;
        --bkg-color-2: white;
        --text-color: #222;
		--text-color-blue: Blue;
		--border-color: #ddd;
		--box-shadow-1: #777777;
		--box-shadow-2: #c6c6c6;
		--selection-text-color: rgb(255, 82, 2);
		--selection-bkg-color: transparent;
    }
}

