@import "../../assets/scss/variable.scss";


.popup-line {
    box-shadow: 0vh 0vh 1vh var(--text-color);
    background: var(--bkg-color);
    border: 1px solid var(--text-color);
    position: absolute;
    z-index: 5;
    p {
        margin: 0;
    }
    ul {
        text-align: left;
        li {
            margin-bottom: 5px;
        }
    }
}
.popup-title {
    // border-bottom: 1px solid var(--text-color);
    border-bottom: 1px solid var(--border-color);
    text-align: left;
    padding: 4px 11px 5px;
    font-size: $ft-s;
    font-weight: bold;
    cursor: pointer;
    .close-btn {
        position: absolute;
        right: 8px;
        top: 2px;
        font-size: 0.9rem;
    }
}
.popup-title:hover {
    // border-color: #00ffad;
    // border-color: #00d300;
    border-color: var(--text-color);
}
.highlight {
    z-index: 10;
    .popup-title {
        // background-color: #00ffad;
        // background-color: #00d300;
        // background-color: var(--text-color);
        background-color: var(--selection-text-color);
        color: var(--bkg-color)!important;
        // color: white!important;
        .close-btn {
            color: var(--bkg-color)!important;
            // color: white!important;
        }
    }
}

.popup-content {
    text-align: left;
}

